html,
body {
  height: 100%; }

html {
  -webkit-font-smoothing: antialiased;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  scrollbar-color: rgba(255, 255, 255, 0.3) rgba(46, 43, 57, 0.3);
  overflow-x: hidden; }
  html::-webkit-scrollbar {
    width: 10px; }
  html::-webkit-scrollbar-thumb {
    background-color: rgba(46, 43, 57, 0.3);
    border-radius: 0; }

.body {
  color: #000;
  font-size: 16px;
  text-rendering: optimizeSpeed;
  font-family: Montserrat, Helvetica, sans-serif; }

*:focus,
button:focus {
  outline: none; }

button:focus {
  box-shadow: none !important; }

button {
  transition: color 0.3s, border-color 0.3s, transform 0.3s,  background-color 0.3s !important; }

svg {
  transition: fill 0.3s, filter 0.3s, stroke 0.3s; }

a {
  position: relative;
  color: #000;
  transition: color 0.3s,  background-color 0.3s, border-color 0.3s !important; }
  a:hover {
    text-decoration: none;
    color: #CC2128; }
  a:focus {
    text-decoration: none;
    color: #000;
    box-shadow: none !important; }

a:not([class]) {
  text-decoration-skip-ink: auto; }

.break:after {
  content: "\A";
  white-space: pre; }

input, button, textarea, select {
  font: inherit; }

input:focus::placeholder,
textarea:focus::placeholder {
  color: transparent; }

img,
video,
audio {
  max-width: 100%;
  height: auto; }

sub,
sup {
  line-height: 0; }

label,
button,
select {
  cursor: pointer; }

input {
  cursor: text; }

[disabled] {
  cursor: default; }

abbr[title],
acronym[title],
dfn {
  cursor: help; }

textarea {
  resize: vertical; }

input,
textarea {
  background-clip: padding-box; }

p:last-child {
  margin-bottom: 0; }

.sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.mobile {
  display: none !important; }
  @media (max-width: 767px) {
    .mobile {
      display: inline-block !important; } }

@media (max-width: 767px) {
  .desktop {
    display: none !important; } }

.center {
  text-align: center; }

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto;
  padding-bottom: 20px; }

.header {
  margin-bottom: 20px;
  padding-top: 20px; }

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%; }
